<template>
	<div class="userBody">
<!-- 		<body  ontouchstart=""> -->
		<!-- 顶部用户信息 -->
			<div class="topBox">
				<a href="javascript:history.go(-1)" class="back"></a>
				<div class="kazari"></div>
				<div class="user-pic">
					<div class="portrait">
							<img id="pic" ref="pic" :src="avatar" height="86" width="86">
						<input id="newPicBtn" type="file" @change="fileUpload" accept="image/*" ref="photoUpload">
					</div>
					<p class="user-name">滑稽滑稽</p>
				</div>
				<div class="user-mes">
					<span>可用余额：100元</span>
					<span>冻结余额：23元</span>
				</div>
			</div>
			<cropper :imgsrc="cropperImg" v-if="isEdit" @confirm="confirmAvatar" @close="closeCropper"></cropper>
			<!-- 功能区域开始 -->
 			<ul class="first-list">
				<li class="title">资金管理</li>
				<li><router-link to="/UserList?type=充值" tag="a"><div><span class="yue"></span><p>充值</p></div></router-link></li>
				<li><router-link to="/UserList?type=提现" tag="a"><div><span class="tixian"></span><p>提现</p></div></router-link></li>
				<li><router-link to="/UserList?type=明细" tag="a"><div><span class="more"></span><p>明细</p></div></router-link></li>
			</ul>
			<ul class="first-list">
				<li class="title">租号管理</li>
				<li><router-link to="/UserList?type=租号订单" tag="a"><div><span class="rent-order"></span><p>租号订单</p></div></router-link></li>
				<li><router-link to="/UserList?type=账号管理" tag="a"><div><span class="account-manage"></span><p>账号管理</p></div></router-link></li>
				<li><router-link to="/UserList?type=出租记录" tag="a"><div><span class="lease-record"></span><p>出租记录</p></div></router-link></li>
			</ul>
			<ul class="first-list">
				<li class="title">虚拟商城</li>
				<li><router-link to="/UserList?type=商城订单" tag="a"><div><span class="point-order"></span><p>商城订单</p></div></router-link></li>
			</ul>
			<ul class="first-list">
				<li class="title">代练专区</li>
				<li><router-link to="/UserList?type=我的发布" tag="a"><div><span class="dailian-publish"></span><p>我的发布</p></div></router-link></li>
				<li><router-link to="/UserList?type=我的接单" tag="a"><div><span class="dailian-accept"></span><p>我的接单</p></div></router-link></li>
			</ul>
			<ul class="first-list testt">
				<li class="title">我的账号</li>
				<li><router-link to="/UserList?type=修改密码" tag="a"><div><span class="point-canyu"></span><p>我的收藏</p></div></router-link></li>
				<li><router-link to="/UserList?type=修改密码" tag="a"><div><span class="point-lock"></span><p>修改密码</p></div></router-link></li>
				<li><router-link to="/UserList?type=提现账号" tag="a"><div><span class="bang-det"></span><p>提现账号</p></div></router-link></li>
				<li><router-link to="/UserList?type=实名认证" tag="a"><div><span class="shiming"></span><p>实名认证</p></div></router-link></li>
				<li><router-link to="/UserList?type=投诉售后" tag="a"><div><span class="complain"></span><p>投诉售后</p></div></router-link></li>
				<!-- <?php if($info['is_distributor']==1){  ?> -->
				<li><router-link to="/UserList?type=分站管理" tag="a"><div><span class="manage"></span><p>分站管理</p></div></router-link></li>
				<!-- <?php } ?> -->
			</ul>
			<div class="bottom_area">
				<a @click="signOut"><button type="button" id="sign_out">退出登录</button></a>
			</div>
	</div>
</template>
<script>
import tipShow from "@/assets/js/tips.js"
import cropper from "./cropper.vue";
import { Toast  } from 'vant';
	export default{
		data(){
			return {
				cropperImg:"",
				isEdit:false,
				avatar:""			
			}
		},
		methods:{
			signOut (){
				this.$store.commit("signOut",false);
				tipShow("已成功退出登录")
				this.$router.back()
			},
			fileUpload:function(e) {
					var thisBox = this.$refs['photoUpload'];
					var files = e.target.files;
					var file = files[0];
					var that=this;
					if (!/image.(png|jpg|jpeg|svg\+xml|gif)/.test(file.type)) { 
						tipShow('选择的文件类型不是图片'); 
						return; 
					}
					const toast = Toast.loading({
					  duration: 0,       // 持续展示 toast
					  forbidClick: true, // 禁用背景点击
					  loadingType: 'spinner',
					  mask:true,
					  message: '正在读取图片'
					});
					var reader = new FileReader();
					reader.onload=(event)=>{
						var str = event.target.result;
						var img = new Image();
						img.onload =()=>{
							this.cropperImg=str;
							this.isEdit=true;
							Toast.clear();
						} 
						img.src = str;       	
					}
				reader.readAsDataURL(file);
			},
			closeCropper(){
				this.isEdit=false;
			},
			confirmAvatar(src){
				this.avatar=src;
				localStorage.setItem("kzg-avatar",src);
				this.$toast('头像更改成功');
				this.isEdit=false;
			}
		},
		mounted:function() {
			this.avatar=localStorage.getItem("kzg-avatar")||require("@/assets/img/sony.jpg")
		},
		components:{
			cropper
		}
	}
</script>
<style scoped>
	@import url("../../assets/css/user.css")
</style>


